/*--------------------------------------------------------------
## Base Layout
--------------------------------------------------------------*/
html, body {
	@include font-smoothing(antialiased);
	height: 100%;
}

html {
	box-sizing: border-box;
	height: 100%;
	min-height: 100%;
	@include scroll-style($grey-light, $grey, 20px);
}

body {
	padding-top: 100px;

	@include mq($xl) {
		margin-top: 60px;
	}
}

.site-main {
	// 100vh - header + footer
	// min-height: calc(100vh - 170px);

	// @include mq($xl) {
	// 	min-height: calc(100vh - 130px);
	// }
}

.page-header {
	margin: 50px 0;
}

*,
*::before,
*::after {
	box-sizing: inherit;
}

.site, .content-area, .site-content, .site-main {
	height: 100%;
}