body {
	padding: 0;
}

.quotes {
	width: 100%;
	background-attachment: fixed;
	background-size: cover;
	background-repeat: no-repeat;
	padding: 30px 0 30px;
	height: auto;
	@include mq(1888, min) {
		height: 100%;
	}
}

.quotes__logo-wrapper {
	display: flex;
    justify-content: center;
	margin-bottom: 50px;
}

.quotes-wrapper {
	display: flex;
	margin-bottom: 40px;

	@include mq(1279) {
		flex-direction: column;
	}
}

.quotes-container {
	// max-width: 1170px;
	margin: 0 auto;
	position: relative;
	background: inherit;
	width: 50%;
	padding: 0 15px;

	@include mq(1279) {
		margin-bottom: 35px;
	}

	@include mq(1279) {
		width: 100%;
	}
}

.quotes-wrap {
	width: 100%;
	min-height: 645px;
	height: 100%;
	background-color: #b1b1b1;
	position: relative;
	z-index: 1;
	background: inherit;
	overflow: hidden;
    padding: 63px 30px 26px;

	&::before {
		content: '';
		position: absolute;
		background: inherit;
		z-index: -1;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		box-shadow: inset 0 0 0 2000px rgba(255, 255, 255, 0.09);
		filter: blur(10px);
		margin: -20px;
	}

	@include mq(1024) {
		padding: 94px 15px 26px;
		min-height: auto;
	}
}

.quotes-text {
	text-align: center;
	color: $white;
	font-family: $font-main;
	font-size: 18px;
	font-weight: 600;
	font-style: italic;
	letter-spacing: -0.34px;
	line-height: 38px;
	position: relative;
	margin-bottom: 66px;

	&::before {
		content: '';
		width: 44px;
		height: 2px;
		background-color: #dd3519;
		position: absolute;
		top: -15px;
		left: 50%;
		transform: translateX(-50%);
	}

	@include mq(1024) {
		font-size: 14px;
		margin-bottom: 36px;

	}
}

.quotes-btn {
	width: 310px;
	height: 80px;
	border-radius: 6px;
	background-color: #dd3519;
	margin: 0 auto;
	display: flex;
	align-items: center;
	justify-content: center;
	color: $white;
	font-family: $font-main;
	font-size: 20px;
	font-weight: 600;
	font-style: normal;
	letter-spacing: 0.1px;
	line-height: 21.6px;
	text-align: center;
	cursor: pointer;
	transition: 200ms ease-in-out;

	&:hover {
		background-color: $white;
		color: #dd3519;
	}

	span {
		display: inline-block;
		width: 20px;
		margin-right: 23px;
		margin-top: 5px;
	}

	@include mq(1024) {
		width: 233px;
		height: 55px;
		font-size: 14px;
	}
}

.quotes-icon-wrapper {
	position: absolute;
    top: -24px;
    right: 58px;
	z-index: 2;
}


// QUOTE REPORT

/*--------------------------------------------------------------
## quote-report
--------------------------------------------------------------*/
.quote-report {
    padding: 0 15px;
	width: 50%;

	@include mq(1279) {
		width: 100%;
	}
}

.quote-report__logo-wrapper {
	display: flex;
    justify-content: center;
    margin-bottom: 73px;
}

.quote-report__title {
	color: #2f3034;
	font-family: $font-main;
	font-size: 40px;
	font-weight: 700;
	font-style: normal;
	letter-spacing: -0.08px;
	line-height: 60px;
	text-align: center;
	margin-bottom: 81px;
}

.quote-report__table-content-wrapper {
	// max-width: 1411px;
	width: 100%;
	margin: 0 auto;
	height: 100%;
	border: 1px solid $white;

	@include mq(640) {
		overflow: scroll;
	}
}

.quote-report__table-content {
    width: 100%;
	position: relative;
	z-index: 1;
	background: inherit;
	overflow: hidden;
	// height: 100%;

	&::before {
		content: '';
		position: absolute;
		background: inherit;
		z-index: -1;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		box-shadow: inset 0 0 0 2000px rgba(255, 255, 255, 0.09);
		filter: blur(10px);
		margin: -20px;
	}

	td {
		vertical-align: middle;
	}

	tr.quote-report__table-row {
		border-bottom: 1px solid #d5d5d5;
		color: $white;
	}

	@include mq(640) {
		min-width: 580px;
	}
}

.quote-report__table-header {
	background-color: rgba(#f7f7f7, 0.3);
	height: 70px;
	border-bottom: 1px solid #d5d5d5;
}

.quote-report__table-cell {
	text-align: start;
	color: $white;
	font-family: $font-main;
	font-size: 20px;
	font-weight: 600;
	font-style: normal;
	letter-spacing: -0.04px;
	padding: 20px 30px 20px 0;

	@include mq(1400) {
		font-size: 14px;
	}
}

.quote-report__table-cell--first {
	padding-left: 61px;
	@include mq(1024) {
		padding-left: 20px;
	}
}

.quote-report__table-row-content--quote {
	padding: 0 30px 0 61px;
	vertical-align: middle;
	height: 110px;
	width: 515px;
	color: $white;
	font-family: $font-main;
	font-size: 16px;
	font-weight: 400;
	font-style: $white;
	letter-spacing: 0.37px;

	@include mq(1400) {
		font-size: 14px;
	}

	@include mq(1024) {
		padding: 0 25px 0 20px;
	}

}
